import { ActivatedRoute } from '@angular/router';
import 'rxjs/add/operator/switchMap';
import { MineService } from "../service/mine.service";
var OrdersComponent = (function () {
    function OrdersComponent(route, mineService) {
        this.route = route;
        this.mineService = mineService;
        this.orders = [];
        this.hasGot = false;
    }
    OrdersComponent.prototype.getOrdersByStatus = function () {
        var _this = this;
        this.route.params.subscribe(function (params) {
            return _this.mineService.getOrdersByStatus(params['orderStatus']).subscribe(function (data) {
                _this.orders = data;
                _this.hasGot = true;
            }, function (err) {
                _this.hasGot = true;
            });
        });
    };
    OrdersComponent.prototype.ngOnInit = function () {
        this.getOrdersByStatus();
    };
    return OrdersComponent;
}());
export { OrdersComponent };



